@import url('https://fonts.googleapis.com/css2?family=Mooli&family=Sulphur+Point:wght@300;400;700&display=swap');

.App {
	text-align: center;
	padding: 2% 5%;
}

.App * {
	box-sizing: border-box;
}

ul {
	padding: 0;
	list-style: none;
}

p {
	margin: 5px 0;
}

input[type='text'],
input[type='number'] {
	padding: 8px 6px 8px 10px;
	border-radius: 4px;
	outline: none;
	border: 3px solid #004c54;
	font-size: 14px;
	line-height: 20px;
	align-self: center;
	height: 38px;
	margin-bottom: 20px;
}

.city {
	width: 250px;
}

#city-search {
	display: flex;
	flex-direction: column;
	margin: auto;
	position: relative;
}

.suggestions {
	width: 250px;
	position: absolute;
	top: 45px;
	z-index: 2;
	align-self: center;
	margin: 0;
	font-size: 14px;
	box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
	text-align: left;
	padding: 8px 6px 8px 10px;
	background-color: #acc0c6;
	cursor: default;
}

.suggestions > li:last-child {
	border-bottom-left-radius: 4px;
	border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
	background-color: #bcdcdb;
}

.event {
	padding: 10px 10px 35px;
	margin-bottom: 10px;
	text-align: left;
	border: 3px solid #004c54;
	border-radius: 4px;
	position: relative;
}

.details-btn {
	position: absolute;
	right: 5px;
	bottom: 5px;
	padding: 5px 8px;
	border-radius: 4px;
	outline: none;
	cursor: pointer;
  background-color: #acc0c6;
  font-family: 'Mooli', sans-serif;
}

.alerts-container {
	position: fixed;
	top: 0px;
	left: 20px;
	width: 250px;
	z-index: 1000;
}

.charts-container {
    display: flex;
		grid-template-columns: 1fr 1fr;
		grid-auto-rows: 200px;
}

@media (max-width: 768px) {
	.charts-container {
    display: flex;
		grid-template-columns: 1fr 1fr;
		grid-auto-rows: 200px;
	}
}
